/* Nav Bar */

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to right top, #65dfc9, #6cdbeb);
  backdrop-filter: blur(2rem);
  background-color: #fff;
  color: #000;
}

.nav-title {
  font-size: 1.5rem;
  margin: 14px 16px;
}

.navbar-links ul {
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-links li {
  list-style: none;
}

.navbar-links li a {
  text-decoration: none;
  color: #000;
  padding: 1rem;
  display: block;
}

.navbar-links li:hover {
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  backdrop-filter: blur(2rem);
}

.toggle-button {
  position: absolute;
  top: 1.25rem;
  right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  text-decoration: none;
  color: #000;
}

/* Landing Page */
.landing-page {
  background: linear-gradient(to right top, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  backdrop-filter: blur(2rem);
  background-color:cornflowerblue;
  height: 100vh;
  padding: 0 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main-heading {
  margin: 0;
  font-size: clamp(38px, 8vw, 78px);
  font-weight: 600;
}

.sub-heading {
  font-size: clamp(10px, 2vw, 30px);
  font-weight: 400;
}

/* About Me */

.about-me {
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  backdrop-filter: blur(2rem);
  background-color:cornflowerblue;
  text-align: center;
  padding: 0 16px;
}

.about-me img {
  height: 400px;
  width: 400px;
  border-radius: 10%;
}

/* Work */
.work {
  background: linear-gradient(to right top, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  backdrop-filter: blur(2rem);
  background-color:cornflowerblue;
  text-align: center;
}

.work img {
  padding: 14px 20px;
}

.project-homepage {
  padding: 14px 0 0;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  height: 83%;
  width: 33%;
  margin: 2% 1%;
  transition: 0.3s;
  animation: ease-in-out;
  display: inline-block;
  text-align: center;
  padding: 0 0 1% 0;
}

.card img {
  text-align: center;
  max-width: 90%;
}

.card:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 15px 0px;
}

.card a {
  color: #000;
  text-decoration: none;
}

.work-ul {
  text-align: left;
  list-style: none;
}

.user-tasks-btn:hover {
  opacity: 0.7;
}

/* Contact */
.contact {
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  backdrop-filter: blur(2rem);
  background-color:cornflowerblue;
  text-align: center;
}

.mail-link {
  color: #000;
}

.contact a {
  color: #000;
  text-decoration: none;
  margin: 14px 16px;
}

.contact p {
  font-weight: 500;
}

/* Media queries */

@media screen and (max-width: 900px) {
  /* Landing Page */
  .sub-heading {
    font-size: clamp(12px, 2.5vw, 32px);
    font-weight: 500;
  }

   /* Work */
   .card {
    width: 90%;
  }

  .card img {
    text-align: center;
  }
}

@media screen and (max-width: 600px) {
  /* Landing Page */
  .toggle-button {
    display: flex;
  }

  .navbar-links {
    display: none;
    width: 100%;
  }

  .navbar-links ul {
    width: 100%;
    flex-direction: column;
  }

  .navbar-links.active {
    display: flex;
  }

  .navbar {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }

  /* About */
  .about-me img {
    height: 250px;
    width: 250px;
    border-radius: 10%;
  }

  /* Contact */
  .contact a {
    color: #000;
    text-decoration: none;
    margin: 14px 16px;
    font-size: clamp(18px, 2.5vw, 38px);
  }
}

@media screen and (max-width: 500px) {
  /* Landing Page */
  .sub-heading {
    font-size: clamp(16px, 3vw, 36px);
    font-weight: 500;
  }

}